*, *::after, *::before {
  box-sizing: border-box;
}

html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 100%;
  overflow-x: hidden;
}

body {
  padding-top: 40px;
  display: relative;
  font-size: 100%;
  font-family: "Graphik-Regular", sans-serif;
