}

/* Base Styles for list and detail angular components */
.cf:before,
.cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.cf:after {
  clear: both;
}

a {
  color: #ea4a55;
  text-decoration: none;
  font-family: "Graphik-Semibold", serif;
}

img {
