.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 110;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: row nowrap;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  /*justify-content: center;*/
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  background: none repeat scroll 0 0 #fff;
}

.sk-spinner-rotating-plane.sk-spinner {
  width: 30px;
  height: 30px;
  background-color: #28a7e9;
  margin: 0 auto;
  -webkit-animation: sk-rotatePlane 1.2s infinite ease-in-out;
  animation: sk-rotatePlane 1.2s infinite ease-in-out;
}

@-webkit-keyframes sk-rotatePlane {
  0% {
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

@keyframes sk-rotatePlane {
  0% {
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

/* end preloader */
/* Auto Hide and show the navigation menu*/
header {
  background: #f5b335;
  height: 40px;
  position: fixed;
  top: 0;
  transition: top 0.6s ease-in-out;
  width: 100%;
  z-index: 105;
}

.nav-down {
  -webkit-transition: top .5s;
  -moz-transition: top .5s;
  transition: top .2s;
}

.nav-up {
  top: -60px;
  -webkit-transition: top .1s;
  -moz-transition: top .1s;
  transition: top .2s;
}

nav.navbar-default {
  /* navbar navbar-default navbar-fixed-top templatemo-nav ha-header ha-header-large*/
  background: #ffffff;
  border: none;
  box-shadow: 0px 2px 8px 0px #333333;
  margin: 0 !important;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  /* div.ha-header-perspective 
       div.ha-header-front  */
}

nav.navbar-default div.container-fluid div.navbar-header button.navbar-toggle {
  /* collapsed */
  margin-top: 17px;
  top: -5px;
  background-color: rgba(28, 36, 65, 0.93);
}

nav.navbar-default div.container-fluid div.navbar-header button.navbar-toggle .icon-bar {
  background-color: #ffffff;
}

nav.navbar-default div.container-fluid div.navbar-header button.navbar-toggle .icon-bar:hover {
  background-color: #ffffff;
}

nav.navbar-default div.container-fluid div.navbar-header a.navbar-brand {
  color: rgba(28, 36, 65, 0.93);
  padding: 0 15px;
}

nav.navbar-default div.container-fluid div.navbar-header a.navbar-brand img {
  height: 85px;
  margin: -15px 0 0 0;
  -webkit-transition: .2s ease-in;
  transition: .2s ease-in;
  background-repeat: no-repeat;
}

nav.navbar-default div.container-fluid div.collapse ul.nav li a {
  -webkit-transition: .5s ease-out;
  transition: .5s ease-out;
}

@media (min-width: 767px) {
  nav.navbar-default {
    height: 85px;
    border-radius: 0;
  }
  nav.navbar-default .navbar-nav > li > a.only-link {
    padding-top: 34px;
  }
  nav.navbar-default a.only-link:after {
    content: '';
    display: block;
    height: 5px;
    width: 0;
    margin: 0;
    transition: width .5s ease-out, background-color .5s ease-out;
  }
  nav.navbar-default a.only-link:hover:after {
    width: 100%;
    background: #45858c;
  }
  nav.navbar-default div.container-fluid div.navbar-header a.navbar-brand img {
    height: 77px;
    margin: 4px 0 0 25px;
    -webkit-transition: .2s ease-in;
    transition: .2s ease-in;
    background-repeat: no-repeat;
  }
  header {
    background: #f5b335;
    height: 85px;
    position: fixed;
    top: 0;
    transition: top 0.6s ease-in-out;
    width: 100%;
    z-index: 105;
  }
  .nav-down {
    -webkit-transition: top .5s;
    -moz-transition: top .5s;
    transition: top .2s;
    opacity: 1;
    height: 0px;
  }
  .nav-up {
    top: -100px;
    -webkit-transition: top .1s;
    -moz-transition: top .1s;
    transition: top .2s;
  }
}

@media (max-width: 767px) {
  nav {
    padding: 0px 0;
  }
  nav .navbar-brand {
    padding: 0 0px;
  }
  nav a {
    font-size: 14px;
  }
  nav.navbar-default div.container-fluid .row div.navbar-header a.navbar-brand img {
    height: 50px;
    margin: 5px 0 0 0;
  }
  .navbar-toggle {
    padding: 9px 8px;
  }
}

