.marquee {
  font-size: 16px;
  height: 450px;
  margin: -40px auto 0 auto;
  padding: 0px 0 0 0;
  margin-bottom: 10px;
}

.marquee .marquee_data {
  display: none;
}

.marquee .marquee_stage_large {
  height: inherit;
  position: relative;
  overflow: hidden;
  margin-top: 1px;
}

.marquee .marquee_container_1, .marquee .marquee_container_2 {
  height: inherit;
  position: absolute;
  top: 24px;
  left: 0;
  width: 100%;
}

.marquee .marquee_container_1 {
  z-index: 101;
}

.marquee .marquee_container_2 {
  z-index: 102;
}

.marquee .marquee_panel {
  height: inherit;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  background-position: center 0px;
  background-size: cover;
}

.marquee .marquee_panel .panel_caption {
  width: 68%;
  position: absolute;
  bottom: 40px;
  left: 0;
  margin-left: 16%;
  font-family: 'Lato', sans-serif;
  padding-bottom: 20px;
}

.marquee .marquee_panel .panel_caption h3 {
  font-family: "Graphik-Regular", sans-serif, serif;
  margin: 0 0 0 0;
  font-weight: 400;
  font-size: 2.7em;
  color: rgba(0, 0, 0, 0.2);
}

.marquee .marquee_panel .panel_caption p {
  margin: 0 50px 15px 50px;
  color: #888;
  font-size: 1.3em;
  color: rgba(255, 255, 255, 0.75);
}

.marquee .marquee_panel .panel_caption a.cta {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0);
  text-transform: uppercase;
  color: #fff;
  font-size: .9em;
  padding: 6px 20px;
  transition: background-color .7s, border .7s;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  text-decoration: none;
}

.marquee .marquee_panel .panel_caption a:hover {
  background-color: rgba(255, 255, 255, 0.6);
  color: #000;
  border-color: rgba(255, 255, 255, 0);
}

.marquee .overlay {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(transparent 45%, rgba(0, 0, 0, 0.7) 70%);
  background-size: 100%;
}

.marquee .marquee_nav {
  position: absolute;
  bottom: 2px;
  left: 0;
  text-align: center;
  width: 100%;
  z-index: 103;
}

.marquee .marquee_nav div {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  margin: 0 8px 0 8px;
  cursor: pointer;
  transition: background-color .4s, border .4s;
}

.marquee .marquee_nav div:hover {
  border-color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
}

.marquee .marquee_nav div.selected {
  background-color: #fff;
}

.marquee .btn {
  width: 39px;
  height: 39px;
  background: rgba(255, 255, 255, 0.2) no-repeat;
  background-size: 50px 30px;
  position: absolute;
  top: 192px;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  transition: background-color .4s, border .4s;
}

.marquee .btn:hover {
  background-color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
}

.marquee .btn.prev {
  left: 20px;
  z-index: 104;
  background-image: url(../images/arrow_left.svg);
  background-position: -11px 2.5px;
}

.marquee .btn.next {
  right: 20px;
  z-index: 105;
  background-image: url(../images/arrow_right.svg);
  background-position: -9px 9px;
}

.marquee .marquee_stage_small {
  height: inherit;
  position: relative;
  overflow: hidden;
}

.marquee .marquee_stage_small .marquee_panel {
  width: 100%;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
}

.device-logo, .device-logo2, .device-logo3 {
  height: 260px;
  position: absolute;
  top: -70%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  margin: 0 0 0 0;
  transition: ease-out .2s;
}

.device-logo:hover, .device-logo2:hover, .device-logo3:hover {
  height: 270px;
  transition: ease-in .2s;
}

.device-logo2, .device-logo3 {
